import React from 'react'
import Button from 'antd/es/button'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import axios from 'axios'

const FriendActionButton = props => {
    const {
        invitingFriends,
        invitedFriends,
        realFriends,
        _id,
        callback,
        accountId,
    } = props

    const acceptFriend = id => {
        axios
            .post('/api/friends/accept', {
                id,
            })
            .then(res => {
                if (res.data.success) {
                    callback()
                }
            })
            .catch(err => {})
    }

    const addFriend = id => {
        axios
            .post('/api/friends/add', {
                id,
            })
            .then(res => {
                if (res.data.success) {
                    callback()
                }
            })
            .catch(err => {})
    }

    const unfriend = id => {
        axios
            .post('/api/friends/unfriend', {
                id,
            })
            .then(res => {
                if (res.data.success) {
                    callback()
                }
            })
            .catch(err => {})
    }

    if (accountId.toString() === _id.toString()) return null

    return invitingFriends.find(
        item => item.friend.toString() === _id.toString()
    )
        ? [
              <Button
                  key="0"
                  onClick={() => acceptFriend(_id)}
                  icon={<PlusCircleOutlined />}
              >
                  Accept
              </Button>,
              <Button
                  key="1"
                  onClick={() => unfriend(_id)}
                  icon={<PlusCircleOutlined />}
              >
                  Decline
              </Button>,
          ]
        : invitedFriends.find(item => item.friend.toString() === _id.toString())
        ? [
              <Button
                  key="2"
                  onClick={() => unfriend(_id)}
                  icon={<PlusCircleOutlined />}
              >
                  Cancel invitation
              </Button>,
          ]
        : realFriends.find(item => item.friend.toString() === _id.toString())
        ? [
              <Button
                  key="3"
                  onClick={() => unfriend(_id)}
                  icon={<PlusCircleOutlined />}
              >
                  Unfriend
              </Button>,
          ]
        : [
              <Button
                  key="4"
                  onClick={() => addFriend(_id)}
                  icon={<PlusCircleOutlined />}
              >
                  Become friends
              </Button>,
          ]
}

export default FriendActionButton
