import React from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'

import Button from 'antd/es/button'

import startProgress from '../../Actions/startProgress'

const GoalCard = withRouter(props => {
    const {
        goal,
        owner,
        available,
        account,
        setEditedGoal,
        setEditGoalOpen,
        setInviteGoal,
    } = props

    return (
        <Card
            type="inner"
            style={{
                width: 200,
                display: 'inline-block',
            }}
            hoverable
            cover={
                <div
                    onClick={() =>
                        props.history.push(
                            '/goals/' + owner._id + '/' + goal.goalId
                        )
                    }
                    style={{
                        backgroundImage:
                            'url(' +
                            (goal.images.length > 0 && goal.images[0]) +
                            ')',
                        height: 200,
                        width: 200,
                        backgroundSize: 'cover',
                    }}
                />
            }
            actions={[
                <>
                    {owner._id === account._id && setEditedGoal && (
                        <Button
                            key="1"
                            onClick={() => {
                                setEditedGoal(goal.goalId)
                                setEditGoalOpen(true)
                            }}
                            icon={<PlusCircleOutlined />}
                        >
                            Edit
                        </Button>
                    )}
                    {(available || owner._id === account._id) && (
                        <Button
                            key="2"
                            onClick={() =>
                                startProgress(
                                    goal.goalId,
                                    owner._id,
                                    account._id
                                )
                            }
                            icon={<PlusCircleOutlined />}
                        >
                            Start a progress
                        </Button>
                    )}
                    {owner._id === account._id && setInviteGoal && (
                        <Button
                            key="3"
                            onClick={() => {
                                setInviteGoal(goal)
                            }}
                            icon={<PlusCircleOutlined />}
                        >
                            Invite friends
                        </Button>
                    )}
                </>,
            ]}
        >
            <Card.Meta title={goal.name} description={goal.description} />
        </Card>
    )
})

export default withRouter(GoalCard)
