import React, { useState, useRef, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import Button from 'antd/es/button'
import Comment from 'antd/es/comment'
import Avatar from 'antd/es/avatar'
import Input from 'antd/es/input'
import Form from 'antd/es/form'
import Upload from 'antd/es/upload'
import PlusOutlined from '@ant-design/icons/PlusOutlined'
import LoadingOutlined from '@ant-design/icons/LoadingOutlined'
import { uploadFile } from '../../utils/uploadFile'

import * as wsActions from '../../websocketActions'
import ProgressMessage from './ProgressMessage'

const Messages = props => {
    const [imageLoading, setImageLoading] = useState(false)
    const { data, state, setState } = props
    const { progress, account } = data
    const { goal } = progress
    const messagesEndRef = useRef(null)

    useEffect(() => {
        if (messagesEndRef && messagesEndRef.current)
            messagesEndRef.current.scrollIntoView({ behavior: 'smooth' })
    }, [props.data.progress.messages.length])
    const getMessages = messages =>
        messages && messages.length
            ? [
                  ...messages.map((message, index) => (
                      <ProgressMessage
                          key={index}
                          message={message}
                          data={data}
                          state={state}
                          setState={setState}
                      >
                          {getMessages(message.replies)}
                      </ProgressMessage>
                  )),
                  <div key="-1" ref={messagesEndRef} />,
              ]
            : null

    return goal ? (
        <>
            <div
                style={{
                    flex: 1,
                    overflow: 'hidden',
                    display: 'flex',
                    flexDirection: 'column',
                }}
            >
                <div
                    style={{
                        flex: 1,
                        overflow: 'auto',
                    }}
                >
                    {getMessages(progress.messages)}
                </div>
                <div>
                    <Comment
                        avatar={
                            <Avatar
                                src={
                                    'https://websiter.s3.us-east-2.amazonaws.com/' +
                                    account._id +
                                    '/avatar'
                                }
                            />
                        }
                        content={
                            <>
                                {state.editedMessage && (
                                    <>
                                        <span>Editing message</span>
                                        <span
                                            style={{ cursor: 'pointer' }}
                                            onClick={() => {
                                                setState({
                                                    ...state,
                                                    replyToMessage: '',
                                                    editedMessage: '',
                                                    messageValue: '',
                                                })
                                            }}
                                        >
                                            Cancel editing
                                        </span>
                                    </>
                                )}
                                {state.replyToMessage && (
                                    <>
                                        <span>Commenting message</span>
                                        <span
                                            style={{ cursor: 'pointer' }}
                                            onClick={() => {
                                                setState({
                                                    ...state,
                                                    replyToMessage: '',
                                                    editedMessage: '',
                                                })
                                            }}
                                        >
                                            Cancel commenting
                                        </span>
                                    </>
                                )}
                                <Form.Item>
                                    <Input.TextArea
                                        autoSize={{ minRows: 4, maxRows: 8 }}
                                        rows={4}
                                        onChange={e =>
                                            setState({
                                                ...state,
                                                messageValue: e.target.value,
                                            })
                                        }
                                        value={state.messageValue}
                                    />
                                </Form.Item>
                                <Form.Item>
                                    <Button
                                        htmlType="submit"
                                        onClick={() =>
                                            wsActions.sendMessage(state, data)
                                        }
                                        type="primary"
                                    >
                                        Send message
                                    </Button>
                                    <Upload
                                        showUploadList={false}
                                        customRequest={({ file }) => {
                                            setImageLoading(true)
                                            uploadFile(
                                                file,
                                                800,
                                                undefined,
                                                url => {
                                                    setImageLoading(false)
                                                    wsActions.sendMessage(
                                                        state,
                                                        data,
                                                        url
                                                    )
                                                }
                                            )
                                        }}
                                    >
                                        <Button style={{ marginLeft: '20px' }}>
                                            {imageLoading ? (
                                                <>
                                                    <LoadingOutlined /> Loading
                                                </>
                                            ) : (
                                                <>
                                                    <PlusOutlined /> Add a photo
                                                </>
                                            )}
                                        </Button>
                                    </Upload>
                                </Form.Item>
                            </>
                        }
                    />
                </div>
            </div>
        </>
    ) : null
}

export default withRouter(Messages)
