import React from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import Button from 'antd/es/button'
import Avatar from 'antd/es/avatar'

const { Meta } = Card

const Wallet = props => {
    return (
        <Card title="Your wallet with all money that you have">
            {props.data.account.wallet &&
                props.data.account.wallet.map((money, index) => (
                    <Card
                        key={index}
                        type="inner"
                        style={{
                            width: 200,
                            display: 'inline-block',
                        }}
                        cover={
                            <span
                                onClick={() =>
                                    props.history.push('/progile/' + money.user)
                                }
                            >
                                <Avatar
                                    src={
                                        'https://websiter.s3.us-east-2.amazonaws.com/' +
                                        money.user +
                                        '/avatar'
                                    }
                                    size={100}
                                />
                            </span>
                        }
                        hoverable
                        actions={[
                            <Button
                                key="1"
                                onClick={() => {
                                    props.history.push('/progile/' + money.user)
                                }}
                                icon={<PlusCircleOutlined />}
                            >
                                Go to the shop
                            </Button>,
                        ]}
                    >
                        <Meta title={money.amount} description={money.user} />
                    </Card>
                ))}
        </Card>
    )
}

export default withRouter(Wallet)
