import axios from 'axios'
import checkAuth from './checkAuth'

export default (props, data, setData, options) => {
    options = {
        forceAuth: true,
        forceActivate: true,
        pullData: true,
        ...(options || {}),
    }
    const isAuthenticated = checkAuth()
    if (options.forceAuth) {
        if (!isAuthenticated) {
            props.history.push({
                pathname: '/login',
                state: props.history.location.pathname,
            })
        }
    }
    if (options.forceActivate) {
        if (
            props.isAuthenticated &&
            data.user &&
            !data.user.status === 'notactivated'
        ) {
            props.history.push('/activate-account')
        }
    }
    if (options.pullData) {
        axios
            .get('/api' + props.history.location.pathname)
            .then(res => {
                if (res.data.status && res.data.status === 'no account')
                    props.history.push('/activate-account')
                if (res.data.account) {
                    sessionStorage.setItem('accountId', res.data.account._id)
                }
                setData({ ...res.data, isAuthenticated })
                return res.data
            })
            .catch(err => {})
    } else return
}
