import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import Tabs from 'antd/es/tabs'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import pullData from '../../utils/pullData'
import Modal from 'antd/es/modal'
import Button from 'antd/es/button'
import Avatar from 'antd/es/avatar'
import axios from 'axios'
import AddPerk from './AddPerk'
import PerkCard from './PerkCard'

const { Meta } = Card
const { TabPane } = Tabs

const WishlistAndPerks = props => {
    const [editedPerk, setEditedPerk] = useState('')
    const [editPerkOpen, setEditPerkOpen] = useState()
    const { data, setData } = props
    const { account } = data

    const editPerk = (
        <Modal
            title="Add item"
            visible={editPerkOpen}
            maskClosable
            closable
            onCancel={() => setEditPerkOpen(false)}
            width="90%"
            footer={
                <Button key="back" onClick={() => setEditPerkOpen(false)}>
                    Close
                </Button>
            }
            destroyOnClose={true}
        >
            <AddPerk
                account={account}
                setEditPerkOpen={setEditPerkOpen}
                setData={setData}
                data={data}
                perkId={editedPerk}
                nickname={account && account._id}
                mode={props.mode}
            />
        </Modal>
    )

    return (
        <Card title="Nice things that you can do or give people that you care">
            <Card
                type="inner"
                style={{ width: 200, display: 'inline-block' }}
                hoverable
                cover={
                    <div
                        style={{
                            height: 200,
                            width: 200,
                            backgroundSize: 'cover',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }}
                    >
                        <PlusCircleOutlined
                            style={{
                                fontSize: '60px',
                            }}
                        />
                    </div>
                }
                actions={[
                    <Button
                        key="1"
                        onClick={() => {
                            setEditedPerk('')
                            setEditPerkOpen(true)
                        }}
                        icon={<PlusCircleOutlined />}
                    >
                        Add item
                    </Button>,
                ]}
            >
                <Meta title="Add an item" description="" />
            </Card>

            {account[props.listKey || props.mode] &&
                account[props.listKey || props.mode].map((perk, index) => (
                    <PerkCard
                        key={index}
                        perk={perk}
                        owner={account}
                        account={account}
                        setEditedPerk={setEditedPerk}
                        setEditPerkOpen={setEditPerkOpen}
                        mode={props.mode}
                    />
                ))}
            {editPerk}
        </Card>
    )
}

export default withRouter(WishlistAndPerks)
