import React, { useEffect, useState } from 'react'
import PageHeader from 'antd/es/page-header'
import Form from 'antd/es/form'
import Button from 'antd/es/button'
import axios from 'axios'
import Input from 'antd/es/input'

import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import pullData from '../../utils/pullData'
import Upload from 'antd/es/upload'
import * as classes from './Account.module.css'

const { Meta } = Card
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons'
import { uploadFile } from '../../utils/uploadFile'
import checkImageExists from '../../utils/checkImageExists'

const Account = props => {
    const [data, setData] = useState()
    const [imageV, setImageV] = useState(Math.floor(Math.random() * 10000))
    const [imageExists, setImageExists] = useState(false)

    useEffect(() => {
        pullData(props, data, setData, {})
    }, [])
    if (!imageExists && data)
        checkImageExists(
            'https://websiter.s3.us-east-2.amazonaws.com/' +
                data.account._id +
                '/avatar',
            setImageExists
        )

    const imageUrl =
        imageExists &&
        data &&
        'https://websiter.s3.us-east-2.amazonaws.com/' +
            data.account._id +
            '/avatar'
    const loading = false

    const onFinish = value => {
        axios
            .post('/api/account', {
                name: value.name,
            })
            .then(res => {})
            .catch(err => {})
    }

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    }
    return data ? (
        <>
            <PageHeader
                className="site-page-header"
                title="My account"
                backIcon={false}
            />
            <Upload
                name="avatar"
                listType="picture-card"
                className={classes.Avatar}
                showUploadList={false}
                customRequest={({ file }) => {
                    console.log(file)
                    uploadFile(file, 800, 'avatar', () => {
                        setImageV(Math.floor(Math.random() * 10000))
                    })
                }}
            >
                {imageUrl ? (
                    <img
                        src={imageUrl + '?version=' + imageV}
                        alt="avatar"
                        style={{ width: '100%' }}
                    />
                ) : (
                    <div>
                        {loading ? <LoadingOutlined /> : <PlusOutlined />}
                        <div className="ant-upload-text">Upload</div>
                    </div>
                )}
            </Upload>
            <Form
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                {...layout}
                initialValues={{
                    name: data.account.name,
                    nickname: data.account._id,
                }}
            >
                <Form.Item
                    label="Name"
                    name="name"
                    rules={[
                        {
                            required: true,
                            message: 'Name is too short',
                            min: 2,
                            whitespace: true,
                        },
                    ]}
                >
                    <Input placeholder="Must be more than 1 character long" />
                </Form.Item>
                <Form.Item
                    label="Nickname"
                    name="nickname"
                    rules={[
                        {
                            required: true,
                            message: 'Nickname is too short',
                            whitespace: true,
                        },
                        {
                            pattern: /^[a-zA-Z0-9_-]*$/,
                            message:
                                "Nickname should contain only letters, numbers, '-' and '_'.",
                        },
                    ]}
                >
                    <Input
                        disabled={true}
                        placeholder="Must be more than 1 character long"
                    />
                </Form.Item>
                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" size="large">
                        Save
                    </Button>
                </Form.Item>
            </Form>
        </>
    ) : null
}

export default withRouter(Account)
