import React, { useEffect, useState } from 'react'
import { Route, Switch, withRouter, Redirect } from 'react-router-dom'

import Logout from './containers/Auth/Logout/Logout'
import Auth from './containers/Auth/Auth'
import AuthTest from './containers/Auth/AuthTest'
import Spinner from './components/UI/Spinner/Spinner'
import './components/UI/loadStyles'
import AccountLayout from './components/Layout/Layout'
import Goals from './containers/Goals/Goals'
import Goal from './containers/Goal/Goal'
import Progresses from './containers/Progresses/Progresses'
import Progress from './containers/Progress/Progress'
import Shop from './containers/Shop/Shop'

import Friends from './containers/Friends/Friends'
import Account from './containers/Account/Account'
import Profile from './containers/Profile/Profile'

import ActivateAccount from './containers/ActivateAccount/ActivateAccount'
import Cookies from 'universal-cookie'
import Perk from './containers/Shop/Perk'
import axios from 'axios'
import jwt from 'jsonwebtoken'
import WishlistItem from './containers/Wishlist/WishlistItem'
import Wishlist from './containers/Wishlist/Wishlist'

const cookies = new Cookies()

const App = () => {
    const [isAuthenticated, setIsAuthenticated] = useState()
    const [account, setAccount] = useState()
    useEffect(() => {
        async function fetchMyAPI() {
            sessionStorage.setItem('systemRefresh', '0')
            const token = cookies.get('auth_token')
            const rememberMe = cookies.get('rememberme')
            if (rememberMe) {
                cookies.remove('rememberme', {
                    path: '/',
                })
                if (rememberMe === 'false') {
                    cookies.remove('auth_token', {
                        path: '/',
                    })
                }
            }
            const userId = jwt.decode(token)
            axios.defaults.headers.post['Content-Type'] = 'application/json'
            axios.defaults.headers.put['Content-Type'] = 'application/json'
            axios.defaults.headers.delete['Content-Type'] = 'application/json'
            axios.defaults.headers.common.Accept = 'application/json'
            if (token && userId) {
                axios.defaults.headers.common['x-auth-token'] = token
                axios.defaults.headers.post['x-auth-token'] = token
                axios.defaults.headers.get['x-auth-token'] = token
                axios.defaults.headers.delete['x-auth-token'] = token
                axios.defaults.headers.put['x-auth-token'] = token
                setIsAuthenticated(userId._id)
            } else {
                setIsAuthenticated(false)
            }
        }
        fetchMyAPI()
    })

    const redirectTo = cookies.get('redirectto')

    if (redirectTo) {
        cookies.remove('redirectto', {
            path: '/',
        })
    }

    const routes = (
        <AccountLayout isAuthenticated={isAuthenticated} account={account}>
            <Switch>
                <Route path="/goals" exact component={Goals} />
                <Route path="/goals" component={Goal} />

                <Route path="/progresses" exact component={Progresses} />
                <Route path="/progresses" component={Progress} />

                <Route path="/profile" component={Profile} />

                <Route path="/friends" exact component={Friends} />

                <Route path="/account" exact component={Account} />

                <Route path="/shop" exact component={Shop} />
                <Route path="/shop" component={Perk} />
                <Route path="/wishlist" exact component={Wishlist} />
                <Route path="/wishlist" component={WishlistItem} />
                {!isAuthenticated && (
                    <Route path="/login" exact component={Auth} />
                )}
                <Route path="/logout" exact component={Logout} />

                <Route
                    path="/activate-account"
                    exact
                    component={ActivateAccount}
                />

                {process.env.NODE_ENV === 'development' && !isAuthenticated && (
                    <Route path="/logintest" exact component={AuthTest} />
                )}

                <Redirect to="/dashboard" />
            </Switch>
        </AccountLayout>
    )

    return typeof isAuthenticated === 'undefined' ? null : redirectTo ? (
        <Redirect to={redirectTo} />
    ) : (
        <>
            <div style={{ height: '100%' }}>{routes}</div>
            {/* {this.props.loading && <Spinner cover />} */}
        </>
    )
}

export default withRouter(App)
