import React from 'react'
import axios from 'axios'
import { withRouter } from 'react-router-dom'
import Button from 'antd/es/button'
import Table from 'antd/es/table'
import Tag from 'antd/es/tag'
import RewardsView from '../../components/RewardsView/RewardsView'
import TimeAgo from 'timeago-react'

const Transactions = props => {
    const { account } = props.data
    if (!account) return null
    const { transactionsData, friendsData } = account

    const confirmTransaction = transactionId => {
        axios
            .post('/api/shop/confirm', {
                transactionId,
            })
            .then(res => {
                if (res.data.success) {
                    props.setData({ ...props.data, ...res.data })
                }
            })
            .catch(err => {})
    }

    const cancelTransaction = transactionId => {
        axios
            .post('/api/shop/cancel', {
                transactionId,
            })
            .then(res => {
                if (res.data.success) {
                    props.setData({ ...props.data, ...res.data })
                }
            })
            .catch(err => {})
    }

    const columns = [
        {
            title: 'Type',
            dataIndex: 'types',
            key: 'types',
            render: types => (
                <>
                    {types.map(tag => {
                        let color = tag.length > 5 ? 'geekblue' : 'green'
                        if (tag === 'loser') {
                            color = 'volcano'
                        }
                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        )
                    })}
                </>
            ),
        },
        {
            title: 'With',
            dataIndex: 'user',
            key: 'user',
        },
        {
            title: 'Item',
            dataIndex: 'perk',
            key: 'perk',
            render: perk => {
                console.log(perk)
                if (perk.simple) return perk.simple
                else
                    return (
                        <RewardsView
                            goal={{
                                rewardsGroups: [{ rewards: [perk], key: 1 }],
                            }}
                            groupKey={1}
                        />
                    )
            },
        },
        {
            title: 'Amount',
            dataIndex: 'amount',
            key: 'amount',
            render: amount => (amount > 0 ? amount : ''),
        },
        {
            title: 'Date',
            dataIndex: 'date',
            key: 'date',
            render: date => <TimeAgo datetime={date} live />,
        },
        {
            title: 'Progress',
            dataIndex: 'progress',
            key: 'progress',
            render: progress => (
                <Button
                    type="link"
                    onClick={() =>
                        props.history.push('/progresses/' + progress)
                    }
                >
                    {progress}
                </Button>
            ),
        },
        {
            title: 'Status',
            key: 'status',
            dataIndex: 'status',
            render: (status, record) => {
                if (status === 'Not confirmed') {
                    return (
                        <>
                            <Button
                                danger={true}
                                type="link"
                                onClick={() =>
                                    cancelTransaction(record.transaction._id)
                                }
                            >
                                Cancel
                            </Button>
                            {record.transaction.from === account._id && (
                                <Button
                                    type="link"
                                    onClick={() =>
                                        confirmTransaction(
                                            record.transaction._id
                                        )
                                    }
                                >
                                    Confirm
                                </Button>
                            )}
                        </>
                    )
                }
                return status
            },
        },
    ]

    const tableData = []
    let sorted = [...transactionsData]
    sorted = sorted.sort((a, b) =>
        new Date(a.date).getTime() < new Date(b.date).getTime() ? 1 : -1
    )
    for (let transaction of sorted) {
        const types = []
        if (transaction.from === account._id && transaction.to === account.id) {
            types.push('internal')
        }
        if (transaction.to === account._id) {
            if (transaction.progress) {
                types.push('earn')
                if (!transaction.amount) types.push('item')
                else types.push('money')
            } else {
                if (transaction.item && transaction.amount) {
                    types.push('buy')
                    types.push('item')
                } else {
                    types.push('recieve gift')
                    if (!transaction.amount) types.push('item')
                    else types.push('money')
                }
            }
        } else if (transaction.from === account._id) {
            if (transaction.progress) {
                types.push('pay')
                if (!transaction.amount) types.push('item')
                else types.push('money')
            } else {
                if (transaction.item && transaction.amount) {
                    types.push('sell')
                    types.push('item')
                } else {
                    types.push('send gift')
                    if (!transaction.amount) types.push('item')
                    else types.push('money')
                }
            }
        }

        const user =
            transaction.to !== account._id ? transaction.to : transaction.from

        tableData.push({
            key: tableData.length,
            types,
            user,
            perk: transaction.item,
            amount: transaction.amount,
            date: transaction.date,
            progress: transaction.progress,
            status: transaction.status,
            transaction,
        })
    }
    console.log(account)
    return <Table columns={columns} dataSource={tableData} />
}

export default withRouter(Transactions)
