import React, { useState, useEffect } from 'react'
import axios from 'axios'
import Modal from 'antd/es/modal'

import Button from 'antd/es/button'
import Input from 'antd/es/input'
import InputNumber from 'antd/es/input-number'
import Form from 'antd/es/form'
import Select from 'antd/es/select'
import Upload from 'antd/es/upload'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import Avatar from 'antd/es/avatar'
import { uploadFile } from '../../utils/uploadFile'

const AddPerk = props => {
    const [perk, setPerk] = useState()
    const [previewImage, setPreviewImage] = useState('')
    const [images, setImages] = useState([])

    const perkId = props.perkId || ''
    useEffect(() => {
        if (perkId)
            axios
                .get(`/api/${props.mode || 'shop'}/${props.nickname}/${perkId}`)
                .then(res => {
                    if (res.data.success) {
                        setPerk(res.data.perk)
                        setImages(
                            res.data.perk.images.map((url, index) => ({
                                url,
                                uid: index,
                            }))
                        )
                    }
                })
                .catch(err => {})
    }, [])

    const onFinish = value => {
        axios
            .post(`/api/${props.mode || 'shop'}/add`, {
                value: { ...value, images: images.map(image => image.url) },
                id: props.perkId,
            })
            .then(res => {
                props.setData({ ...props.data, ...res.data })
                props.setEditPerkOpen(false)
            })
            .catch(err => {})
    }

    const deletePerk = () => {
        if (perkId) {
            axios
                .post(`/api/${props.mode || 'shop'}/delete/${perkId}`)
                .then(res => {
                    props.setData({ ...props.data, ...res.data })
                })
                .catch(err => {})
        }
        props.setEditPerkOpen(false)
    }

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    }

    const connections = props.account.friendsData || []

    const connectionsSelect = (
        <Select
            mode="multiple"
            style={{ width: '100%' }}
            placeholder="Please select"
        >
            {connections.map(item => (
                <Select.Option key={item._id}>
                    <Avatar
                        src={
                            'https://websiter.s3.us-east-2.amazonaws.com/' +
                            item._id +
                            '/avatar'
                        }
                        size={20}
                        style={{ marginRight: '10px' }}
                    />
                    {item.name} - ({item._id})
                </Select.Option>
            ))}
        </Select>
    )
    const [form] = Form.useForm()

    const normFile = e => {
        if (Array.isArray(e)) {
            return e
        }
        return e && e.fileList.map(image => image.url)
    }

    return !props.perkId || perk ? (
        <>
            <Form
                form={form}
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                initialValues={
                    perk || {
                        name: 'New item',
                    }
                }
                {...layout}
            >
                <Form.Item
                    name="images"
                    label="Images"
                    valuePropName="images"
                    getValueFromEvent={normFile}
                >
                    <Upload
                        listType="picture-card"
                        fileList={images.map((image, index) => ({
                            ...image,
                            uid: index,
                        }))}
                        onPreview={file => {
                            setPreviewImage(file.url)
                        }}
                        onChange={({ fileList }) => setImages(fileList)}
                        customRequest={({ file }) => {
                            uploadFile(file, 800, undefined, url => {
                                setImages(
                                    [...images, { url }].map(
                                        (image, index) => ({
                                            ...image,
                                            uid: index,
                                        })
                                    )
                                )
                            })
                        }}
                    >
                        {images.length >= 8 ? null : (
                            <div>
                                <PlusOutlined />
                                <div className="ant-upload-text">Upload</div>
                            </div>
                        )}
                    </Upload>
                </Form.Item>
                <Form.Item
                    label="Name"
                    name="name"
                    rules={[
                        {
                            required: true,
                            message: 'Please input the item name.',
                        },
                    ]}
                >
                    <Input />
                </Form.Item>
                <Form.Item label="Description" name="description">
                    <Input.TextArea placeholder="Write a few words about why this item." />
                </Form.Item>

                {props.mode === 'shop' && (
                    <Form.Item label="Available for" name="users">
                        {connectionsSelect}
                    </Form.Item>
                )}

                {props.mode === 'shop' && (
                    <Form.Item label="Price" name="price">
                        <InputNumber />
                    </Form.Item>
                )}

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" size="large">
                        Save
                    </Button>

                    {perkId && (
                        <Button
                            type="link"
                            onClick={deletePerk}
                            danger
                            size="large"
                        >
                            Delete
                        </Button>
                    )}
                </Form.Item>
            </Form>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </>
    ) : null
}

export default AddPerk
