import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import Tabs from 'antd/es/tabs'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import pullData from '../../utils/pullData'
import Modal from 'antd/es/modal'
import Button from 'antd/es/button'
import Avatar from 'antd/es/avatar'
import axios from 'axios'

const { Meta } = Card
const { TabPane } = Tabs

const Progresses = props => {
    const [data, setData] = useState(
        props.progressesData && props.account ? { ...props } : {}
    )

    useEffect(() => {
        if (!props.progressesData || !props.account)
            pullData(props, data, setData, {})
    }, [])

    const { account, progressesData } = data

    const ProgressCard = withRouter(props => {
        const { progress } = props
        const { goal } = progress
        return (
            <Card
                type="inner"
                style={{
                    width: 200,
                    display: 'inline-block',
                }}
                hoverable
                cover={
                    <div
                        onClick={() =>
                            props.history.push('/progresses/' + progress._id)
                        }
                        style={{
                            backgroundImage:
                                goal &&
                                'url(' +
                                    (goal.images.length > 0 && goal.images[0]) +
                                    ')',
                            height: 200,
                            width: 200,
                            backgroundSize: 'cover',
                        }}
                    />
                }
            >
                {goal && (
                    <>
                        <Meta title={goal.name} />
                        <Meta
                            title={'Made by ' + progress.worker}
                            description={'In goal by ' + progress.owner}
                        />
                    </>
                )}
            </Card>
        )
    })
    return account ? (
        <>
            <Tabs defaultActiveKey="1">
                <TabPane tab="All roles" key="1">
                    <Card title="All progresses where I have a role">
                        {progressesData &&
                            progressesData.map((progress, index) => (
                                <ProgressCard key={index} progress={progress} />
                            ))}
                    </Card>
                </TabPane>
                <TabPane tab="Achiever" key="2">
                    <Card title="All progresses where I am achieving goals">
                        {progressesData &&
                            progressesData
                                .filter(
                                    progress => progress.worker === account._id
                                )
                                .map((progress, index) => (
                                    <ProgressCard
                                        key={index}
                                        progress={progress}
                                    />
                                ))}
                    </Card>
                </TabPane>
                <TabPane tab="Motivator" key="3">
                    <Card title="All progresses where I motivate friends to achieve goals">
                        {progressesData &&
                            progressesData
                                .filter(
                                    progress => progress.owner === account._id
                                )
                                .map((progress, index) => (
                                    <ProgressCard
                                        key={index}
                                        progress={progress}
                                    />
                                ))}
                    </Card>
                </TabPane>
                <TabPane tab="Expert" key="4">
                    <Card title="All progresses where I check and approve progresses">
                        {progressesData &&
                            progressesData
                                .filter(
                                    progress =>
                                        progress.goal &&
                                        (progress.goal.experts.includes(
                                            account._id
                                        ) ||
                                            progress.goal.milestonesValues.find(
                                                milestone =>
                                                    milestone.experts.includes(
                                                        account._id
                                                    )
                                            ))
                                )
                                .map((progress, index) => (
                                    <ProgressCard
                                        key={index}
                                        progress={progress}
                                    />
                                ))}
                    </Card>
                </TabPane>
                <TabPane tab="Supporter" key="5">
                    <Card title="All progresses where I support friend to achieve goals">
                        {progressesData &&
                            progressesData
                                .filter(
                                    progress =>
                                        progress.goal &&
                                        (progress.goal.supporters.includes(
                                            account._id
                                        ) ||
                                            progress.goal.milestonesValues.find(
                                                milestone =>
                                                    milestone.supporters.includes(
                                                        account._id
                                                    )
                                            ))
                                )
                                .map((progress, index) => (
                                    <ProgressCard
                                        key={index}
                                        progress={progress}
                                    />
                                ))}
                    </Card>
                </TabPane>
            </Tabs>
        </>
    ) : null
}

export default withRouter(Progresses)
