import React, { useEffect, useState } from 'react'
import PageHeader from 'antd/es/page-header'
import Avatar from 'antd/es/avatar'
import Card from 'antd/es/card'

import { withRouter } from 'react-router-dom'
import FriendActionButton from '../../components/FriendActionButton'
import FriendCard from '../../components/FriendCard'
import pullData from '../../utils/pullData'
import Tabs from 'antd/es/tabs'
import Progresses from '../Progresses/Progresses'
import GoalCard from '../Goals/GoalCard'
import Wallet from '../Shop/Wallet'
import PerkCard from '../Shop/PerkCard'

const Profile = props => {
    const [data, setData] = useState()

    useEffect(() => {
        pullData(props, data, setData, {
            forceAuth: false,
            forceActivate: false,
        })
    }, [props.history.location.pathname])
    if (!data || !data.profile) return null

    const { profile, account } = data
    const imageUrl =
        profile &&
        profile.image &&
        'https://websiter.s3.us-east-2.amazonaws.com/' + profile._id + '/avatar'

    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []
    if (account && account.friends) {
        for (let friend of account.friends) {
            if (friend.status === 'friend') realFriends.push(friend)
            else if (friend.status === 'invited') invitedFriends.push(friend)
            else if (friend.status === 'inviting') invitingFriends.push(friend)
        }
    }

    return data ? (
        <>
            <PageHeader
                className="site-page-header"
                title={profile.name}
                backIcon={false}
            />
            <div>Nickname - {profile._id}</div>
            <Avatar src={imageUrl} size={200} />
            <FriendActionButton
                accountId={account ? account._id : ''}
                realFriends={realFriends}
                invitedFriends={invitedFriends}
                invitingFriends={invitingFriends}
                _id={profile._id}
                callback={() =>
                    pullData(props, data, setData, {
                        forceAuth: false,
                        forceActivate: false,
                    })
                }
            />
            <Tabs>
                <Tabs.TabPane tab="Friends" key={1}>
                    <Card title="Friends">
                        {profile.friends
                            .filter(friend => friend.status === 'friend')
                            .map(friend => {
                                const friendData = {
                                    ...profile.friendsData.find(
                                        item =>
                                            item._id.toString() ===
                                            friend.friend.toString()
                                    ),
                                    ...friend.friend,
                                }

                                return (
                                    <FriendCard
                                        key={friendData._id}
                                        {...props}
                                        account={account}
                                        friend={friendData}
                                        realFriends={realFriends}
                                        invitingFriends={invitingFriends}
                                        invitedFriends={invitedFriends}
                                        data={data}
                                        setData={setData}
                                    />
                                )
                            })}
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Progresses" key={2}>
                    <Card title="Friends">
                        <Progresses
                            account={profile}
                            progressesData={profile.progressesData}
                        />
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Goals" key={3}>
                    <Card title="Goals">
                        {profile.goals.map((goal, index) => (
                            <GoalCard
                                key={index}
                                goal={goal}
                                owner={profile}
                                available={goal.users.includes(account._id)}
                                account={account}
                            />
                        ))}
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Shop" key={4}>
                    <Card title="Shop">
                        {profile.perks &&
                            profile.perks.map((perk, index) => (
                                <PerkCard
                                    key={index}
                                    perk={perk}
                                    owner={profile}
                                    account={account}
                                    onlyBuy={true}
                                    data={data}
                                    setData={setData}
                                />
                            ))}
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Wallet" key={5}>
                    <Card title="Wallet">
                        <Wallet data={{ account: profile }} />
                    </Card>
                </Tabs.TabPane>
                <Tabs.TabPane tab="Wishlist" key={6}>
                    <Card title="Friends">
                        {profile.friends
                            .filter(friend => friend.status === 'friend')
                            .map(friend => {
                                const friendData = {
                                    ...profile.friendsData.find(
                                        item =>
                                            item._id.toString() ===
                                            friend.friend.toString()
                                    ),
                                    ...friend.friend,
                                }

                                return (
                                    <FriendCard
                                        key={friendData._id}
                                        {...props}
                                        account={account}
                                        friend={friendData}
                                        realFriends={realFriends}
                                        invitingFriends={invitingFriends}
                                        invitedFriends={invitedFriends}
                                        data={data}
                                        setData={setData}
                                    />
                                )
                            })}
                    </Card>
                </Tabs.TabPane>
            </Tabs>
        </>
    ) : null
}

export default withRouter(Profile)
