import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import Tabs from 'antd/es/tabs'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import pullData from '../../utils/pullData'
import Modal from 'antd/es/modal'
import Button from 'antd/es/button'
import AddGoal from './AddGoal'
import Avatar from 'antd/es/avatar'
import axios from 'axios'
import GoalCard from './GoalCard'

const { Meta } = Card
const { TabPane } = Tabs

const Goals = props => {
    const [data, setData] = useState({})
    const [editedGoal, setEditedGoal] = useState('')
    const [editGoalOpen, setEditGoalOpen] = useState(false)
    const [inviteGoal, setInviteGoal] = useState(false)

    useEffect(() => {
        pullData(props, data, setData, {})
    }, [])

    const { account } = data

    const editGoal = (
        <Modal
            title="Add goal"
            visible={editGoalOpen}
            maskClosable
            closable
            onCancel={() => setEditGoalOpen(false)}
            width="90%"
            footer={
                <Button key="back" onClick={() => setEditGoalOpen(false)}>
                    Close
                </Button>
            }
            destroyOnClose={true}
        >
            <AddGoal
                account={account}
                setEditGoalOpen={setEditGoalOpen}
                setData={setData}
                data={data}
                goalId={editedGoal}
                nickname={account && account._id}
            />
        </Modal>
    )
    const inviteToGoalModal = account && (
        <Modal
            title="Invite friends to work on your goal"
            visible={inviteGoal}
            maskClosable
            closable
            onCancel={() => setInviteGoal(false)}
            footer={
                <Button key="back" onClick={() => setInviteGoal(false)}>
                    Close
                </Button>
            }
            destroyOnClose={true}
        >
            {account.friendsData
                .filter(
                    friend =>
                        !(
                            inviteGoal.progresses &&
                            inviteGoal.progresses.find(
                                item => item.user === friend._id
                            )
                        )
                )
                .map((friend, index) => (
                    <div key={index}>
                        <span
                            style={{ cursor: 'pointer' }}
                            onClick={() =>
                                props.history.push('/profile/' + friend._id)
                            }
                        >
                            <Avatar
                                src={
                                    'https://websiter.s3.us-east-2.amazonaws.com/' +
                                    friend._id +
                                    '/avatar'
                                }
                                size={20}
                            />
                            {friend.name} - ({friend._id})
                        </span>
                        <Button
                            onClick={() =>
                                startProgress(
                                    inviteGoal.goalId,
                                    account._id,
                                    friend._id
                                )
                            }
                        >
                            Invite
                        </Button>
                    </div>
                ))}
        </Modal>
    )

    return account ? (
        <>
            <Tabs defaultActiveKey="1">
                <TabPane tab="My goals" key="1">
                    <Card title="Important goals for me and people I care">
                        <Card
                            type="inner"
                            style={{ width: 200, display: 'inline-block' }}
                            hoverable
                            cover={
                                <div
                                    style={{
                                        height: 200,
                                        width: 200,
                                        backgroundSize: 'cover',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                    }}
                                >
                                    <PlusCircleOutlined
                                        style={{
                                            fontSize: '60px',
                                        }}
                                    />
                                </div>
                            }
                            actions={[
                                <Button
                                    key="1"
                                    onClick={() => {
                                        setEditedGoal('')
                                        setEditGoalOpen(true)
                                    }}
                                    icon={<PlusCircleOutlined />}
                                >
                                    Add goal
                                </Button>,
                            ]}
                        >
                            <Meta title="Add a goal" description="" />
                        </Card>

                        {account.goals &&
                            account.goals.map((goal, index) => (
                                <GoalCard
                                    key={index}
                                    goal={goal}
                                    owner={account}
                                    account={account}
                                    setEditedGoal={setEditedGoal}
                                    setEditGoalOpen={setEditGoalOpen}
                                    setInviteGoal={setInviteGoal}
                                />
                            ))}
                    </Card>
                </TabPane>
                <TabPane tab="Available goals" key="2">
                    <Card title="Important goals that I can work on">
                        {account.friendsData &&
                            [account, ...account.friendsData].map(
                                (friend, indexG) =>
                                    friend.goals.map((goal, index) =>
                                        goal.users.includes(account._id) ||
                                        friend._id === account._id ? (
                                            <GoalCard
                                                key={indexG + '_' + index}
                                                goal={goal}
                                                owner={friend}
                                                available={goal.users.includes(
                                                    account._id
                                                )}
                                                account={account}
                                                setEditedGoal={setEditedGoal}
                                                setEditGoalOpen={
                                                    setEditGoalOpen
                                                }
                                                setInviteGoal={setInviteGoal}
                                            />
                                        ) : null
                                    )
                            )}
                    </Card>
                </TabPane>
                <TabPane tab="My friends' goals" key="3">
                    <Card title="Important goals created by my friends">
                        {account.friendsData &&
                            account.friendsData.map((friend, indexG) =>
                                friend.goals.map((goal, index) => (
                                    <GoalCard
                                        key={indexG + '_' + index}
                                        goal={goal}
                                        owner={friend}
                                        available={goal.users.includes(
                                            account._id
                                        )}
                                        account={account}
                                        setEditedGoal={setEditedGoal}
                                        setEditGoalOpen={setEditGoalOpen}
                                        setInviteGoal={setInviteGoal}
                                    />
                                ))
                            )}
                    </Card>
                </TabPane>
            </Tabs>
            {editGoal}
            {inviteToGoalModal}
        </>
    ) : null
}

export default withRouter(Goals)
