import React, { useState, useEffect } from 'react'

import Card from 'antd/es/card'
import Button from 'antd/es/button'
import Modal from 'antd/es/modal'
import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'
import PageHeader from 'antd/es/page-header'

import Input from 'antd/es/input'
import Form from 'antd/es/form'

import axios from 'axios'
import pullData from '../../utils/pullData'
import FriendCard from '../../components/FriendCard'
const { Meta } = Card

const Friends = props => {
    const [addFriendOpen, setAddFriendOpen] = useState()
    const [pendingFriendsOpen, setPendingFriendsOpen] = useState()
    const [searchResult, setSearchResult] = useState(null)
    const [data, setData] = useState()

    useEffect(() => {
        pullData(props, data, setData, {})
    }, [])
    if (!data) return null
    const { account } = data
    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []

    if (account.friendsData && account.friends) {
        for (let friend of account.friendsData) {
            const friendRef = account.friends.find(
                item => item.friend.toString() === friend._id.toString()
            )
            const fullFriend = { ...friendRef, ...friend }
            if (friendRef) {
                if (friendRef.status === 'friend') realFriends.push(fullFriend)
                else if (friendRef.status === 'invited')
                    invitedFriends.push(fullFriend)
                else if (friendRef.status === 'inviting')
                    invitingFriends.push(fullFriend)
            }
        }
    }

    const onFinish = value => {
        axios
            .post('/api/friends/find', {
                ...value,
            })
            .then(res => {
                if (!res.data.success) {
                    alert('Accounts with this nickname do not exist.')
                    setSearchResult(null)
                } else setSearchResult(res.data.friend)
            })
            .catch(err => {})
    }

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    }

    const addFriendModal = (
        <Modal
            title="Add friend"
            visible={addFriendOpen}
            maskClosable
            closable
            onCancel={() => setAddFriendOpen(false)}
            footer={
                <Button key="back" onClick={() => setAddFriendOpen(false)}>
                    Return
                </Button>
            }
        >
            <p>To find a friend you need to enter your friend's nickname.</p>
            <Form
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                {...layout}
            >
                <Form.Item
                    label="Search"
                    name="search"
                    rules={[
                        {
                            required: true,
                            message: 'Search field is too short',
                            min: 2,
                        },
                    ]}
                >
                    <Input placeholder="Must be more than 2 characters long" />
                </Form.Item>

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" size="large">
                        Search
                    </Button>
                </Form.Item>
            </Form>
            {searchResult && (
                <Card title="Search result">
                    <FriendCard
                        {...props}
                        friend={searchResult}
                        realFriends={realFriends}
                        invitingFriends={invitingFriends}
                        invitedFriends={invitedFriends}
                        data={data}
                        setData={setData}
                    />
                </Card>
            )}
        </Modal>
    )

    const pendingFriendsModal = (
        <Modal
            title="Pending friends"
            visible={pendingFriendsOpen}
            maskClosable
            closable
            onCancel={() => setPendingFriendsOpen(false)}
            footer={
                <Button key="back" onClick={() => setPendingFriendsOpen(false)}>
                    Return
                </Button>
            }
        >
            {invitingFriends.length > 0 && (
                <Card title="People invited me to be friends">
                    {invitingFriends.map(friend => (
                        <FriendCard
                            key={friend._id}
                            {...props}
                            friend={friend}
                            realFriends={realFriends}
                            invitingFriends={invitingFriends}
                            invitedFriends={invitedFriends}
                            data={data}
                            setData={setData}
                        />
                    ))}
                </Card>
            )}

            {invitedFriends.length > 0 && (
                <Card title="I invited these people">
                    {invitedFriends.map(friend => (
                        <FriendCard
                            key={friend._id}
                            {...props}
                            friend={friend}
                            realFriends={realFriends}
                            invitingFriends={invitingFriends}
                            invitedFriends={invitedFriends}
                            data={data}
                            setData={setData}
                        />
                    ))}
                </Card>
            )}
        </Modal>
    )

    return (
        <>
            <PageHeader
                className="site-page-header"
                title="My friends"
                backIcon={false}
            />

            {(invitedFriends.length > 0 || invitingFriends.length > 0) && (
                <Button
                    style={{ margin: '30px 0px ' }}
                    type="primary"
                    onClick={() => setPendingFriendsOpen(true)}
                >
                    Show pending friends
                </Button>
            )}
            <Card>
                <Card
                    onClick={() => setAddFriendOpen(true)}
                    type="inner"
                    style={{ width: 200, display: 'inline-block' }}
                    cover={
                        <div
                            style={{
                                height: 200,
                                width: 200,
                                backgroundSize: 'cover',
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                            }}
                        >
                            <PlusCircleOutlined
                                style={{
                                    fontSize: '60px',
                                }}
                            />
                        </div>
                    }
                >
                    <Meta title="Add a friend" />
                </Card>

                {realFriends.map(friend => (
                    <FriendCard
                        key={friend._id}
                        {...props}
                        friend={friend}
                        realFriends={realFriends}
                        invitingFriends={invitingFriends}
                        invitedFriends={invitedFriends}
                        data={data}
                        setData={setData}
                    />
                ))}
            </Card>
            {addFriendModal}
            {pendingFriendsModal}
        </>
    )
}

export default Friends
