import React from 'react'

import Card from 'antd/es/card'
import Avatar from 'antd/es/avatar'

import pullData from '../utils/pullData'
import FriendActionButton from './FriendActionButton'
const { Meta } = Card

const FriendCard = props => (
    <Card
        type="inner"
        style={{ width: 200, display: 'inline-block' }}
        cover={
            <span
                onClick={() =>
                    props.history.push('/profile/' + props.friend._id)
                }
            >
                <Avatar
                    src={
                        props.friend.image &&
                        'https://websiter.s3.us-east-2.amazonaws.com/' +
                            props.friend._id +
                            '/avatar'
                    }
                    size={100}
                />
            </span>
        }
        actions={[
            <FriendActionButton
                accountId={props.account ? props.account._id : ''}
                realFriends={props.realFriends}
                invitedFriends={props.invitedFriends}
                invitingFriends={props.invitingFriends}
                _id={props.friend._id}
                callback={() =>
                    pullData(props, props.data, props.setData, {
                        forceAuth: false,
                        forceActivate: false,
                    })
                }
            />,
        ]}
    >
        <Meta title={props.friend.name} description={props.friend._id} />
    </Card>
)

export default FriendCard
