import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import pullData from '../../utils/pullData'
import PageHeader from 'antd/es/page-header'
import Descriptions from 'antd/es/descriptions'
import Tabs from 'antd/es/tabs'
import Divider from 'antd/es/divider'
import Modal from 'antd/es/modal'
import Avatar from 'antd/es/avatar'

const { Item } = Descriptions

const Perk = props => {
    const [data, setData] = useState({})
    const [previewImage, setPreviewImage] = useState('')
    useEffect(() => {
        pullData(props, data, setData, {})
    }, [])

    const { perk, profile } = data

    const getAccounts = items => {
        if (profile && profile.friendsData) {
            return items.map(item => {
                const friendObj = profile.friendsData.find(
                    friend => friend._id === item
                )
                if (friendObj) {
                    return (
                        <span
                            style={{ cursor: 'pointer' }}
                            onClick={() =>
                                props.history.push('/profile/' + item)
                            }
                        >
                            <Avatar
                                src={
                                    'https://websiter.s3.us-east-2.amazonaws.com/' +
                                    item +
                                    '/avatar'
                                }
                                size={20}
                                style={{ marginRight: '10px' }}
                            />
                            {friendObj.name} - ({item})
                        </span>
                    )
                } else return item
            })
        } else return items
    }

    return perk ? (
        <>
            <PageHeader
                className="site-page-header"
                title={perk.name}
                backIcon={false}
            />
            {perk.images.map(image => (
                <span onClick={() => setPreviewImage(image)}>
                    <Card
                        type="inner"
                        style={{
                            width: 200,
                            display: 'inline-block',
                        }}
                        hoverable
                        cover={
                            <div
                                style={{
                                    backgroundImage: 'url(' + image + ')',
                                    height: 200,
                                    width: 200,
                                    backgroundSize: 'cover',
                                }}
                            />
                        }
                    />
                </span>
            ))}
            <p>{perk.description}</p>

            <Descriptions
                title="Goal description"
                bordered
                column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
            >
                <Item label="Available for">{getAccounts(perk.users)}</Item>
                <Item label="Price">{perk.price}</Item>
            </Descriptions>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </>
    ) : null
}

export default withRouter(Perk)
