import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import Tabs from 'antd/es/tabs'
import pullData from '../../utils/pullData'
import Perks from './Perks'
import Wallet from './Wallet'
import Transactions from './Transactions'
import WishlistAndPerks from './WishlistAndPerks'

const Shop = props => {
    const [data, setData] = useState({})
    useEffect(() => {
        async function fetchMyAPI() {
            await pullData(props, data, setData, {})
        }
        fetchMyAPI()
    }, [])

    return data.account ? (
        <Tabs>
            <Tabs.TabPane tab="My shop" key={1}>
                {/* <Perks data={data} setData={setData} /> */}
                <WishlistAndPerks
                    data={data}
                    setData={setData}
                    mode="shop"
                    listKey="perks"
                />
            </Tabs.TabPane>
            <Tabs.TabPane tab="Wallet" key={3}>
                <Wallet data={data} />
            </Tabs.TabPane>
            <Tabs.TabPane tab="Transactions" key={4}>
                <Transactions data={data} setData={setData} />
            </Tabs.TabPane>
        </Tabs>
    ) : (
        <span />
    )
}

export default withRouter(Shop)
