import React from 'react'
import axios from 'axios'
import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'

import PlusCircleOutlined from '@ant-design/icons/PlusCircleOutlined'

import Button from 'antd/es/button'

const { Meta } = Card

const PerkCard = props => {
    const { perk, owner, account, setEditedPerk, setEditPerkOpen } = props

    const buyPerk = () => {
        const currency = account.wallet.find(item => item.user === owner._id)
        if (currency && currency.amount >= perk.price) {
            axios
                .post('/api/shop/buy', {
                    perkId: perk.perkId,
                    ownerId: owner._id,
                })
                .then(res => {
                    if (res.data.success) {
                        props.setData({ ...props.data, ...res.data })
                    }
                })
                .catch(err => {})
        } else {
            alert(
                'You cannot buy this item as you do not have enough money from this user.'
            )
        }
    }
    return (
        <Card
            type="inner"
            style={{
                width: 200,
                display: 'inline-block',
            }}
            hoverable
            cover={
                <div
                    onClick={() =>
                        props.history.push(
                            '/shop/' + owner._id + '/' + perk.perkId
                        )
                    }
                    style={{
                        backgroundImage:
                            'url(' +
                            (perk.images.length > 0 && perk.images[0]) +
                            ')',
                        height: 200,
                        width: 200,
                        backgroundSize: 'cover',
                    }}
                />
            }
            actions={[
                <>
                    {owner._id === account._id && !props.onlyBuy && (
                        <Button
                            key="1"
                            onClick={() => {
                                setEditedPerk(perk.perkId)
                                setEditPerkOpen(true)
                            }}
                            icon={<PlusCircleOutlined />}
                        >
                            Edit
                        </Button>
                    )}
                    {props.mode === 'shop' && (
                        <Button
                            key="1"
                            onClick={() => {
                                buyPerk()
                            }}
                            icon={<PlusCircleOutlined />}
                        >
                            Buy
                        </Button>
                    )}
                </>,
            ]}
        >
            <Meta title={perk.name} description={perk.price} />
        </Card>
    )
}

export default withRouter(PerkCard)
